import React from "react";

import {
  List,
  Box,
  Title,
  TimelineContainer,
  Dot,
  Timeline,
  Body,
  Bold,
  Light,
} from "./elements";

const BoxComponent = ({ title, body, numBoxes, isFirst, isLast }) => (
  <Box numBoxes={numBoxes}>
    {
      // need to put gray border in Title and Body so that
      // orange border can go above
    }
    <Title hideBorder={isLast}>{title}</Title>
    <TimelineContainer>
      <Timeline hideTimeline={isFirst} />
      <Dot>{"\u2022"}</Dot>
      <Timeline hideTimeline={isLast} />
    </TimelineContainer>
    <Body hideBorder={isLast}>
      <Bold>{body.primary}</Bold>
      <Light>{body.secondary}</Light>
    </Body>
  </Box>
);

const ScheduleBoxList = ({ boxes }) => (
  <List>
    {boxes.map((boxProps, index) => (
      <BoxComponent
        key={boxProps.title}
        isFirst={index === 0}
        isLast={index === boxes.length - 1}
        numBoxes={boxes.length}
        {...boxProps}
      />
    ))}
  </List>
);

export default ScheduleBoxList;
