import React from "react";
import styled from "@emotion/styled";
import { gothamFonts } from "styles/fonts";
import { padding } from "polished";
import colors from "styles/colors";
import { breakPoints } from "components/Grid.tsx";

const Wrapper = styled("div")({
  flexBasis: "33.333333%",
  display: "flex",
  flexDirection: "column",
  alignItems: "flex-start",
  ...padding(0, 60, 40, 0),

  "@media (max-width: 1200px)": {
    flexBasis: "50%",
    paddingRight: 48,
  },

  [breakPoints.medium]: {
    flexBasis: "100%",
    alignItems: "center",
    textAlign: "center",
    paddingRight: 0,
  },
});

const Icon = styled("img")(({ iconWidth, iconHeight }) => ({
  width: iconWidth,
  height: iconHeight,
  marginBottom: 24,
}));

const Title = styled("h3")(({ light }) => ({
  ...gothamFonts.h3,
  paddingBottom: 24,
  marginBottom: 0,
  color: light ? colors.white : colors.black,

  [breakPoints.medium]: {
    ...gothamFonts["body-xl-bold"],
  },
}));

const SimpleValueProp = ({ icon, title, content, light = false }) => (
  <Wrapper>
    <Icon src={icon} iconWidth={56} iconHeight={56} />
    <Title light={light}>{title}</Title>
    {content}
  </Wrapper>
);

export default SimpleValueProp;
