import React from "react";

import { Row, Column } from "components/Grid.tsx";
import LightFAQItem from "components/LightFAQItem";
import { Desktop, Mobile, MobileFAQ, Question, Answer } from "./elements";

const faqs = [
  {
    question: "Am I eligible to apply?",
    answer: (
      <span>
        Companies must make a minimum of $100,000 in monthly sales and have been
        in operation for at least 1 year to be eligible for Brexs ecommerce
        card. Sole Proprietorships are not eligible.
      </span>
    ),
    showMobile: true,
  },

  {
    question: "How does Brex make money?",
    answer: (
      <span>
        Brex doesnt charge you fees or interest. Instead, we simply receive a
        small fee from the merchant for each purchase you make with your Brex
        card.
      </span>
    ),
    showMobile: false,
  },

  {
    question: "What are the payment terms?",
    answer: (
      <span>
        Each day of spend has a 60-day float. Brex will charge you for todays
        spend in 60 days, and tomorrows spend in 61 days. 100% of the amount
        due on the 60th day must be repaid.
      </span>
    ),
    showMobile: false,
  },

  {
    question: "How is my credit limit determined?",
    answer: (
      <span>
        Brex determines your credit limit up to 75% of your projected monthly
        sales, forecasted using the sales data you provide through your Shopify
        integration (or similar sales platform). Companies can get credit limits
        up to $5 million.
      </span>
    ),
    showMobile: true,
  },
  {
    question: "Will applying for Brex affect my personal credit score?",
    answer: (
      <span>
        No. Applying for Brex, whether your application is approved or rejected,
        will never affect your personal credit score.
      </span>
    ),
    showMobile: false,
  },

  {
    question: "Are there any additional fees?",
    answer: (
      <span>
        Nope. Unlike with traditional loans, merchant cash advances, working
        capital loans, or most other financing options, Brex doesnt charge fees
        or interest, period.
      </span>
    ),
    showMobile: true,
  },
];

const FAQ = () => (
  <React.Fragment>
    <Desktop>
      {faqs.map((faq) => (
        <LightFAQItem {...faq} key={faq.question} />
      ))}
    </Desktop>
    <Mobile>
      <Row padding={60} gutterWidth={26}>
        <Column span={0} spanMedium={12}>
          {faqs
            .filter((faq) => faq.showMobile)
            .map(({ question, answer }) => (
              <MobileFAQ key={question}>
                <Question>{question}</Question>
                <Answer>{answer}</Answer>
              </MobileFAQ>
            ))}
        </Column>
      </Row>
    </Mobile>
  </React.Fragment>
);

export default FAQ;
