import React from "react";

import {
  Section,
  Title,
  SlideshowContainer,
  DesktopArrow,
  Slideshow,
  SlideOuter,
  SlideInner,
  LogoWrapper,
  Logo,
  QuoteWrapper,
  Quote,
  Author,
  AuthorTitle,
  IndicatorsContainer,
  MobileArrow,
  Indicators,
  Indicator,
} from "./elements";

const Testimonial = ({ logo, quote, author }) => (
  <SlideOuter>
    <SlideInner>
      <LogoWrapper>
        <Logo src={logo.file} height={logo.height} width={logo.width} />
      </LogoWrapper>
      <QuoteWrapper>
        <Quote>{quote}</Quote>
        <Author>
          {author.name}
          ,&nbsp;
          <AuthorTitle>{author.title}</AuthorTitle>
        </Author>
      </QuoteWrapper>
    </SlideInner>
  </SlideOuter>
);

class TestimonialsComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedIndex: 0,
    };

    this.handleScroll = this.handleScroll.bind(this);
    this.goToIndex = this.goToIndex.bind(this);
    this.slideshowRef = React.createRef();
  }

  componentDidMount() {
    const slideshow = this.slideshowRef.current;
    slideshow.scrollTo(0, 0);
  }

  setIndexState(newIndex) {
    const { selectedIndex } = this.state;
    if (newIndex !== selectedIndex) {
      this.setState({
        selectedIndex: newIndex,
      });
    }
  }

  handleScroll(e) {
    const { currentTarget } = e;
    const { scrollWidth, scrollLeft } = currentTarget;
    const { messages } = this.props;
    const { scrolling } = this.state;

    if (scrolling) {
      return;
    }

    const newIndex = Math.round(messages.length * (scrollLeft / scrollWidth));
    this.setIndexState(newIndex);
  }

  goToIndex(newIndex) {
    const { messages } = this.props;
    const { selectedIndex } = this.state;

    if (newIndex === selectedIndex) {
      return;
    }

    if (newIndex < 0 || newIndex >= messages.length) {
      return;
    }

    const slideshow = this.slideshowRef.current;
    if (!slideshow) {
      return;
    }

    const { scrollWidth } = slideshow;
    const newScrollLeft = scrollWidth * ((1.0 * newIndex) / messages.length);
    slideshow.scrollTo(newScrollLeft, 0);
    this.setIndexState(newIndex);
  }

  render() {
    const { messages, title } = this.props;
    const { selectedIndex } = this.state;

    return (
      <Section>
        <Title>{title}</Title>
        <SlideshowContainer>
          <DesktopArrow
            direction="left"
            onClick={() => this.goToIndex(selectedIndex - 1)}
            disabled={selectedIndex === 0}
          />
          <Slideshow onScroll={this.handleScroll} ref={this.slideshowRef}>
            {messages.map((testimonial) => (
              <Testimonial key={testimonial.key} {...testimonial} />
            ))}
          </Slideshow>
          <DesktopArrow
            direction="right"
            onClick={() => this.goToIndex(selectedIndex + 1)}
            disabled={selectedIndex === messages.length - 1}
          />
        </SlideshowContainer>
        <IndicatorsContainer>
          <MobileArrow
            direction="left"
            onClick={() => this.goToIndex(selectedIndex - 1)}
            disabled={selectedIndex === 0}
          />
          <Indicators>
            {messages.map((testimonial, index) => (
              <Indicator
                key={testimonial.key}
                selected={index === selectedIndex}
                onClick={() => this.goToIndex(index)}
              >
                {"\u2022"}
              </Indicator>
            ))}
          </Indicators>
          <MobileArrow
            direction="right"
            onClick={() => this.goToIndex(selectedIndex + 1)}
            disabled={selectedIndex === messages.length - 1}
          />
        </IndicatorsContainer>
      </Section>
    );
  }
}

// abstracting out so that this is potentially reusable
const Testimonials = ({ messages, title }) => (
  <TestimonialsComponent messages={messages} title={title} />
);
export default Testimonials;
